<template>
  <div >
    <cms-breadcrumb to="/memberGrade" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>带 * 为必填</li>
        <li>加入会员等级权益必须进行权益设置</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData.memberLevel" label-width="210px">
        <el-form-item label="会员等级名称"  prop="levelName" :error='errorMessage.levelName'>
          <el-input v-model="formData.memberLevel.levelName" :autofocus="true" ref="first" ></el-input>
          <div style="color:#666666">会员账户<span style="color:#999999">用于登录商城，请确保唯一</span></div>
        </el-form-item>
        <el-form-item label="等级升级条件"  prop="levelExp" :error='errorMessage.levelExp' >
          <el-input v-model="formData.memberLevel.levelExp" ></el-input>
        </el-form-item>
      </el-form>
      <h3 class="cms-segmentation">会员等级权益</h3>
      <div class="grade-items">
        <div class="grade-item">
          <el-row>
            <el-col :span="10"><div class="grade-img"><img src="https://img13.360buyimg.com/n2/jfs/t4039/255/1270201086/64578/259c6361/5871db9dN47fe1b30.jpg" ></div></el-col>
            <el-col :span="14">
                <span class="grade-item-title" >购物福利</span><br />
                <span style="font-size:14px;">订单支付完成之后有机会获得不同奖励</span>
            </el-col>
          </el-row>
          <el-row  class="grade-item-cont">
            <el-col :span="24">
                <span>随机获得50-100积分</span><br />
                <span>仅限2018-12-30 23:59:59内使用</span>
            </el-col>
          </el-row>
          <el-row  class="grade-item-foot">
            <el-col :span="24">
                <el-checkbox  style="margin:0 15px;" ></el-checkbox>
                <el-button size="small" @click="setInterests">权益设置</el-button>
                <el-button size="small" @click="rest">清空设置</el-button>
            </el-col>
          </el-row>
        </div>
        <div class="grade-item">
          <el-row>
            <el-col :span="10"><div class="grade-img"><img src="https://img12.360buyimg.com/n7/jfs/t17989/208/1590957743/451183/f4572fbf/5ad05c50N3acc4ab3.jpg" ></div></el-col>
            <el-col :span="14">
                <span class="grade-item-title">生日特权</span><br />
                <span style="font-size:14px;">生日享有系统赠送的奖励</span>
            </el-col>
          </el-row>
          <el-row class="grade-item-cont">
            <el-col :span="24">
                <span>可获得100元优惠券</span><br />
                <span>仅限生日当天使用（当天23:59:59）</span>
            </el-col>
          </el-row>
          <el-row class="grade-item-foot">
            <el-col :span="24">
                <el-checkbox style="margin:0 15px;" ></el-checkbox>
                <el-button size="small"  @click="setInterests">权益设置</el-button>
                <el-button size="small" @click="rest">清空设置</el-button>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="cms-form-footer" >
        <cms-button  @click="save(true)"  v-color >保存</cms-button>
        <!-- <cms-button type='rest' @click="rest">重置</cms-button>  -->
        <!-- <cms-button type='rest' @click="test">mock测试</cms-button> -->
      </div>
      <el-dialog
        title="权益设置"
        :visible.sync="interestsDialog"
        width="40%">
        <el-form ref="form" :rules="rules" :model="formData.levelRightList[0]" label-width="140px">
          <el-form-item  label="权益奖励类型:"  prop="type" :error='errorMessage.type'>
            <el-radio-group v-model="formData.levelRightList[0].type">
              <el-radio :label="1">成长值</el-radio>
              <el-radio :label="2">积分</el-radio>
              <el-radio :label="3">优惠券</el-radio>
            </el-radio-group>
            <div class="detail">优惠券额度为全平台通用</div>
          </el-form-item>
          <el-form-item  label="派发方式:"  prop="sendwayType" :error='errorMessage.sendwayType'>
            <el-radio-group v-model="formData.levelRightList[0].sendwayType">
              <el-radio :label="1">系统自为</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item  label="奖励额度:"  prop="rewardTypeCode" :error='errorMessage.sendwayType'>
            <div>
              <el-radio v-model="formData.levelRightList[0].rewardTypeCode" label="1">固定</el-radio>
              <el-input type="namber" :min="formData.levelRightList[0].minValue" :max="formData.levelRightList[0].maxValue"  style="width:70%;margin-left:10px;"></el-input>
              <el-radio v-model="formData.levelRightList[0].rewardTypeCode" label="2">随机</el-radio>
            </div>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="interestsDialog = false">取 消</el-button>
          <el-button type="primary" @click="interestsDialog = false">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { memberlevelAdd } from './../../../../api/users/level.js'
// import list from '@/mixins/list'
var defaultData = {
  'memberLevel':
    { 'levelName': '',
      'levelExp': '' },
  'levelRightList': [{
    'type': '',
    'rewardTypeCode': '',
    'sendwayType': '',
    'minValue': 10,
    'maxValue': 50,
    'couponId': ''
  }]
}
export default {
  mixins: [form],
  data() {
    return {
      interestsDialog: false,
      formData: Object.assign({}, defaultData),
      rules: {
        levelName: [rules.required('此项为必填')],
        levelExp: [rules.required('此项为必填'), rules.number()],
        typeCode: [rules.required('此项为必填')],
        sendwayType: [rules.required('此项为必填')],
        rewardTypeCode: [rules.required('此项为必填')]
      }
    }
  },
  methods: {
    save(state) { // 添加
      console.log(state)
      const queryData = {}
      queryData.memberLevel = this.formData.memberLevel
      this.saveForm(memberlevelAdd, queryData, state, '/memberGrade/list', 'list')
    },
    setInterests() {
      this.interestsDialog = true
    },
    test() {
      this.timer = setInterval(this.mockData, 600)
    },
    rest() { // 重置表单
      window.clearInterval(this.timer)
      this.$refs.form.resetFields()
      this.formData = Object.assign({}, defaultData)
      this.$refs.first.focus()
    }
  }
}
</script>
<style lang="scss" scoped>
.grade-items{
  width: 100%;
  padding: 30px 0 50px 210px;
  display: flex;
  // justify-content: left;
}
.grade-img{
  width: 100px;
  height:85px;
  overflow: hidden;
  img{
    max-width: 85%;
    height: auto;
  }
}
.grade-item{
  width: 260px;
  margin-right: 150px;
  line-height: 18px;
  color: #666666;
}
.grade-item-cont{
  background-color: #E5CFA0;
  color: #fff;
  font-size: 14px;
  padding: 10px;
}
.grade-item-foot{
  margin-top: 10px;
}
.grade-item-title{
  font-size: 16px;
  line-height: 30px;
  font-weight: bold;
}
.el-form-item .detail{
  color: #999999;
  font-size: 13px;
}
</style>





